import { About, sertifikat, projects } from '../../DATA'

class DataPortofolio {
  static async listSertifikat () {
    const sertificates = await sertifikat
    const loading = document.querySelector('.loading')
    loading.style.display = 'flex'
    return sertificates
  }

  static async listProjects () {
    const project = await projects
    const loading = document.querySelector('.loading')
    loading.style.display = 'flex'
    return project
  }

  static async descriptionText () {
    const text = await About
    const loading = document.querySelector('.loading')
    loading.style.display = 'flex'
    return text
  }

  static async detailSertifikat (id) {
    const detailSertif = await sertifikat.find(sertif => sertif.id.toLowerCase() === id)
    const loading = document.querySelector('.loading')
    loading.style.display = 'flex'
    return detailSertif
  }
}

export default DataPortofolio
