import DataPortofolio from '../../data/data-portofolio'
import UrlParse from '../../routes/url-parse'
import { detailGambarSertifikat } from '../templates/template-creator'

const DetailSertifPage = {
  async render () {
    return `
        <section class="content">
            <div class="content__title">
                <h2 class="content__label">Detail Sertifikat</h2>
            </div>
            <div id="content__detail"></div>

            <div class="loading">
              <div class="lds-grid"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
            </di>
        </section>
        `
  },

  async afterRender () {
    const url = UrlParse.parseActiveUrlWithoutCombiner()
    const sertifikat = await DataPortofolio.detailSertifikat(url.id)
    const loading = document.querySelector('.loading')
    const mainContent = document.querySelector('#content__detail')

    mainContent.innerHTML = detailGambarSertifikat(sertifikat)
    loading.style.display = 'none'
  }
}

export default DetailSertifPage
