import routes from '../routes/routes'
import UrlParse from '../routes/url-parse'
import DrawerInitiator from '../utils/drawer-initiator'
import LinkInitiator from '../utils/link-initiator'
import HashCahngeInitiator from '../utils/hash-change-initiator'

class App {
  constructor ({ button, drawer, body, content, listItem, backgorundChange, colorChange }) {
    this._button = button
    this._drawer = drawer
    this._body = body
    this._content = content
    this._listItem = document.querySelectorAll(listItem)
    this._backgorundChange = backgorundChange
    this._colorChange = colorChange

    this._initialAppShell()
  }

  _initialAppShell () {
    DrawerInitiator.init({
      button: this._button,
      drawer: this._drawer,
      body: this._body
    })

    LinkInitiator.init()

    HashCahngeInitiator.init({
      allLink: this._listItem,
      background: this._backgorundChange,
      color: this._colorChange
    })
  }

  async renderPage () {
    const url = UrlParse.parseActiveUrlWithCombiner()
    const pages = routes[url]
    const skipLinkElem = document.querySelector('.langsung')
    skipLinkElem.addEventListener('click', (event) => {
      event.preventDefault()
      document.querySelector('#main-content').focus()
    })

    try {
      this._content.innerHTML = await pages.render()
      await pages.afterRender()
    } catch (error) {
      this._content.innerHTML = `
      <h2 class="error">${url} halaman tersebut tidak ditemukan</h2>
      `
      alert(error)
    }
  }
}

export default App
