class HeroElement extends HTMLElement {
  connectedCallback () {
    this.render()
  }

  render () {
    this.innerHTML = `
        <div class="hero">
            <div class="hero__title">
                <div class="hero__decoration">
                    <p class="hero__label">Hello, I'm Rayhan Zulfitri</p>
                </div>
            </div>
        </div>
    `
  }
}

customElements.define('hero-element', HeroElement)
