\n    transform: translate(-300px, 0);\r\n    opacity: 0;\r\n    transition: .5s ease;\r\n    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset, rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;\r\n    z-index: 10;\r\n    max-height: 0;\r\n}\r\n\r\n.list__nav {\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.item__nav {\r\n    padding: 10px;\r\n    width: 100%;\r\n    display: list-item;\r\n    text-align: left;\r\n    transition: 1s ease;\r\n}\r\n\r\n.item__nav:hover {\r\n    transition: .5s ease;\r\n    transform: scale(1.1);\r\n}\r\n\r\n.item__nav a{\r\n    color: #E2DCC8;\r\n    text-decoration: none;\r\n    font-size: 30px;\r\n    height: 100%;\r\n    width: 100%;\r\n    /* font-weight: bold; */\r\n}\r\n\r\n.open {\r\n    max-height: 500px;\r\n    -webkit-transform: translate(0, 0);\r\n    transform: translate(0, 0);\r\n    opacity: 1;\r\n}\r\n\r\n/*\r\n Hero Element\r\n */\r\n\r\n.hero__title {\r\n    background-image: url('../public/background.jpg');\r\n    background-size: cover;\r\n    background-position: center;\r\n    width: 100%;\r\n    min-height: 380px;\r\n    margin-top: 120px;\r\n    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset;\r\n}\r\n\r\n.hero__decoration {\r\n    width: 100%;\r\n    min-height: 380px;\r\n    background-color: #100f0f73;\r\n    display: flex;\r\n}\r\n\r\n.hero__label{\r\n    margin: auto;\r\n    color: #E2DCC8;\r\n}\r\n\r\n/*\r\n Main content\r\n */\r\n\r\nmain {\r\n    width: 100%;\r\n}\r\n\r\n.content__title {\r\n    background-color: #0F3D3E;\r\n    text-align: center;\r\n    padding: 1em;\r\n}\r\n\r\n.content__label {\r\n    color: #E2DCC8;\r\n}\r\n\r\n/*\r\nHome\r\n */\r\n\r\n.home {\r\n    padding: 1.6em;\r\n    column-gap: 10px;\r\n    display: grid;\r\n    grid-template-columns: repeat(2, 1fr);\r\n}\r\n\r\n.home__about {\r\n    padding: 1.5em;\r\n}\r\n\r\n.about__title{\r\n    text-align: center;\r\n    margin-bottom: 5px;\r\n}\r\n\r\n/*\r\n Learning\r\n */\r\n\r\n#content__learning{\r\n    width: 100%;\r\n    padding: 1em;\r\n    display: grid;\r\n    grid-template-columns: repeat(1, 1fr);\r\n    row-gap: 20px;\r\n}\r\n\r\n.learning__container {\r\n    background-color: #E2DCC8;\r\n    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;\r\n}\r\n\r\n.learning__container img {\r\n    width: 100%;\r\n}\r\n\r\n.learning__link {\r\n    padding: .5em;\r\n    font-size: 1.5em;\r\n    transition: .5s ease-in-out;\r\n}\r\n\r\n.learning__link a {\r\n    text-decoration: none;\r\n    color: #100F0F;\r\n    transition: .5s ease-in-out;\r\n}\r\n\r\n/*\r\n Detail\r\n */\r\n\r\n#content__detail {\r\n    width: 100%;\r\n    padding: .5em;\r\n}\r\n\r\n.detail {\r\n    background-color: #0F3D3E;\r\n    border-radius: 10px;\r\n    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;\r\n}\r\n\r\n.detail__title {\r\n    border-radius: 10px 10px 0 0;\r\n    padding: .5em;\r\n    background-color: #E2DCC8;\r\n    color: #0F3D3E;\r\n}\r\n\r\n.detail__images{\r\n    padding: .5em;\r\n}\r\n\r\n#content__detail img {\r\n    width: 100%;\r\n}\r\n\r\n.detail__label{\r\n    text-align: center;\r\n}\r\n\r\n.detail__description {\r\n    background-color: #E2DCC8;\r\n    border-bottom: 1px solid #100F0F;\r\n    font-weight: bold;\r\n}\r\n\r\n.description__date {\r\n    padding: .5em;\r\n}\r\n\r\n.detail__link {\r\n    background-color: #E2DCC8;\r\n    border-radius: 0 0 10px 10px;\r\n    padding: .5em;\r\n    text-align: center;\r\n}\r\n\r\n.detail__link a {\r\n    text-decoration: none;\r\n    font-size: 2em;\r\n    padding: 3px;\r\n    border-radius: 5px;\r\n    color: #E2DCC8;\r\n    background-color: #0F3D3E;\r\n    font-weight: bold;\r\n}\r\n\r\n/*\r\n Project\r\n */\r\n\r\n#content__project {\r\n    width: 100%;\r\n    padding: 1em;\r\n    display: grid;\r\n    justify-content: center;\r\n    grid-template-columns: repeat(1, 1fr);\r\n    row-gap: 20px;\r\n}\r\n\r\n.project__container {\r\n    background-color: #E2DCC8;\r\n    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;\r\n}\r\n\r\n.project__container h3{\r\n    text-align: center;\r\n}\r\n\r\n.project__container p{\r\n    text-align: justify;\r\n}\r\n\r\n.project__link {\r\n    padding: .5em;\r\n    font-size: 1.5em;\r\n    transition: .5s ease-in-out;\r\n}\r\n\r\n.project__link a {\r\n    text-decoration: none;\r\n    color: #100F0F;\r\n    transition: .5s ease-in-out;\r\n}\r\n\r\n.project__link:hover, .project__link a:hover {\r\n    color: #0F3D3E;\r\n    transform: scale(1.04);\r\n}\r\n\r\n.project__container p {\r\n    padding: 1em;\r\n}\r\n\r\n/*\r\n Footer\r\n */\r\n\r\nfooter-element {\r\n    margin: 0;\r\n    padding: 0;\r\n    width: 100%;\r\n    z-index: -1;\r\n    position: absolute;\r\n}\r\n\r\nfooter {\r\n    background-color: #0F3D3E;\r\n}\r\n\r\n.footer__link {\r\n    height: 100px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.footer__link a {\r\n    text-decoration: none;\r\n    color: #E2DCC8;\r\n    margin: 0 10px;\r\n    transition: .4s ease-in-out;\r\n}\r\n\r\n.learning__link:hover, .learning__link a:hover {\r\n    color: #0F3D3E;\r\n    transform: scale(1.04);\r\n}\r\n\r\n.footer__anchor {\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.footer__anchor i {\r\n    font-size: 44px;\r\n}\r\n\r\n.footer__link a:hover {\r\n    color: #E2DCC8;\r\n    transform: scale(1.04);\r\n}\r\n\r\n/* Loading */\r\n.lds-grid {\r\n    display: inline-block;\r\n    position: relative;\r\n    width: 80px;\r\n    height: 80px;\r\n  }\r\n  .lds-grid div {\r\n    position: absolute;\r\n    width: 16px;\r\n    height: 16px;\r\n    border-radius: 50%;\r\n    background: #0F3D3E;\r\n    animation: lds-grid 1.2s linear infinite;\r\n  }\r\n  .lds-grid div:nth-child(1) {\r\n    top: 8px;\r\n    left: 8px;\r\n    animation-delay: 0s;\r\n  }\r\n  .lds-grid div:nth-child(2) {\r\n    top: 8px;\r\n    left: 32px;\r\n    animation-delay: -0.4s;\r\n  }\r\n  .lds-grid div:nth-child(3) {\r\n    top: 8px;\r\n    left: 56px;\r\n    animation-delay: -0.8s;\r\n  }\r\n  .lds-grid div:nth-child(4) {\r\n    top: 32px;\r\n    left: 8px;\r\n    animation-delay: -0.4s;\r\n  }\r\n  .lds-grid div:nth-child(5) {\r\n    top: 32px;\r\n    left: 32px;\r\n    animation-delay: -0.8s;\r\n  }\r\n  .lds-grid div:nth-child(6) {\r\n    top: 32px;\r\n    left: 56px;\r\n    animation-delay: -1.2s;\r\n  }\r\n  .lds-grid div:nth-child(7) {\r\n    top: 56px;\r\n    left: 8px;\r\n    animation-delay: -0.8s;\r\n  }\r\n  .lds-grid div:nth-child(8) {\r\n    top: 56px;\r\n    left: 32px;\r\n    animation-delay: -1.2s;\r\n  }\r\n  .lds-grid div:nth-child(9) {\r\n    top: 56px;\r\n    left: 56px;\r\n    animation-delay: -1.6s;\r\n  }\r\n  @keyframes lds-grid {\r\n    0%, 100% {\r\n      opacity: 1;\r\n    }\r\n    50% {\r\n      opacity: 0.5;\r\n    }\r\n  }\r\n  \r\n  .loading {\r\n    width: 100%;\r\n    display: none;\r\n    justify-content: center;\r\n    align-items: center;\r\n  }"],"sourceRoot":""}]);
