import DataPortofolio from '../../data/data-portofolio'
import { createElementHome } from '../templates/template-creator'

const HomePage = {
  async render () {
    return `
        <section class="content">
            <div class="content__title">
                <h2 class="content__label">Home</h2>
            </div>
            <div id="content__home"></div>
            <div class="loading">
              <div class="lds-grid"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
            </di>
        </section>
        `
  },

  async afterRender () {
    const textDescription = await DataPortofolio.descriptionText()
    const loading = document.querySelector('.loading')
    const homeContainer = document.querySelector('#content__home')

    homeContainer.innerHTML = createElementHome(textDescription)
    loading.style.display = 'none'
  }
}

export default HomePage
