import DataPortofolio from '../../data/data-portofolio'
import { createProjectPage } from '../templates/template-creator'

const ProjectPage = {
  async render () {
    return `
        <section class="content">
            <div class="content__title">
                <h2 class="content__label">Projects</h2>
            </div>
            <div id="content__project"></div>

            <div class="loading">
              <div class="lds-grid"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
            </di>
        </section>
        `
  },

  async afterRender () {
    const allProjects = await DataPortofolio.listProjects()
    const loading = document.querySelector('.loading')
    const projectContainer = document.querySelector('#content__project')

    projectContainer.innerHTML = createProjectPage(allProjects)
    loading.style.display = 'none'
  }
}

export default ProjectPage
