const createElementHome = (descriptions) => `
    <div class="home">
        ${descriptions.map(description => `<p class="home__description">${description.description}</p>`).join('')}
    </div>
    <div class="home__about">
        <h3 class="about__title">About Me</h3>
        <p class="about__desc">Bernama lengkap Rayhan Zulfitri Dwi Cahyo. Lulus di SMK Negeri 1 Madiun pada tahun 2019 dan melanjutkan pendidikan di UNIPMA(Universitas PGRI Madiun) semester 7 insyaallah lulus tepat waktu, walaupun tidak tepat sesuai yang saya harapkan.</p>
    </div>
`

const createLerningPage = (learning) => `
    ${learning.map(list => `
    <div class="learning__container">
        <img
        src="${list.images[0]}"
        alt="Sertifikat ${list.nameSertif}"
        />
        <h3 class="learning__link"><a href="#/detail/${list.id}">${list.nameSertif}</a></h3>
    </div>
    `)
    .join('')
}
`

const detailGambarSertifikat = (sertifikat) => `
    <div class="detail">
        <div class="detail__title">
            <h3 class="detail__label">${sertifikat.nameSertif}</h3>
        </div>
        <div class="detail__images">
            ${sertifikat.images.map(image => `
            <img
            src="${image}"
            alt="Sertifikat ${sertifikat.nameSertif}"
            />
            `)
            .join('')}
        </div>
        <div class="detail__description">
            <p class="description__date">Given to: ${sertifikat.givenTo}</p>
            <p class="description__date">Valid until: ${sertifikat.validUntil}</p>
        </div>
        <div class="detail__link">
            <p><a href="${sertifikat.linkSertif}" target="_blank" rel="noopener">Go to official page</a></p>
        </div>
    </div>
`

const createProjectPage = (project) => `
    ${project.map(list => `
    <div class="project__container">
        <h3 class="project__link"><a href="${list.linkProject}" target="_blank" rel="noopener">${list.nameProject}</a></h3>
        <p>${list.aboutProject}</p>
    </div>
    `)
    .join('')
}
`

export {
  createElementHome,
  createLerningPage,
  detailGambarSertifikat,
  createProjectPage
}
