import DataPortofolio from '../../data/data-portofolio'
import { createLerningPage } from '../templates/template-creator'

const LearningPage = {
  async render () {
    return `
        <section class="content">
            <div class="content__title">
                <h2 class="content__label">Learnings and Sertifications</h2>
            </div>
            <div id="content__learning"></div>

            <div class="loading">
              <div class="lds-grid"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
            </di>
        </section>
        `
  },

  async afterRender () {
    const sertifications = await DataPortofolio.listSertifikat()
    const loading = document.querySelector('.loading')
    const listContainer = document.querySelector('#content__learning')

    listContainer.innerHTML = createLerningPage(sertifications)
    loading.style.display = 'none'
  }
}

export default LearningPage
