import 'regenerator-runtime'
import '../styles/style.css'
import '../styles/responsive.css'
import './components/header-element'
import './components/hero-element'
import './components/footer-element'
import 'bootstrap-icons/font/bootstrap-icons.css'
import App from './views/app'
import swRegister from './utils/sw-register'
import UrlParse from './routes/url-parse'

const app = new App({
  button: document.querySelector('#menu'),
  drawer: document.querySelector('.navi'),
  body: document.querySelector('body'),
  content: document.querySelector('#main-content'),
  listItem: '.item__nav',
  backgorundChange: '#E2DCC8',
  colorChange: '#0F3D3E'
})

window.addEventListener('hashchange', () => {
  app.renderPage()
})

window.addEventListener('load', () => {
  app.renderPage()
  swRegister()
})

console.log(UrlParse.parseActiveUrlWithCombiner())
